<template>
  <div class="home">
    <h1 @click="handleclick()">开始录音</h1>
    <h1 @click="handleclicks()">结束录音</h1>
    <h1 @click="handledownloadWAV()">save</h1>
  </div>
</template>

<script>

import Recorder from 'js-audio-recorder'

import axios from 'axios'
let recorder = new Recorder({
    sampleBits: 16,         // 采样位数，支持 8 或 16，默认是16
    sampleRate: 11025,      // 采样率，支持 11025、16000、22050、24000、44100、48000，根据浏览器默认值，我的chrome是48000
    numChannels: 1,         // 声道，支持 1 或 2， 默认是1
    compiling: false,       // 是否边录边转换，默认是false
});
export default {
  name: 'home',
  methods: {
    handleclick () {
      recorder.start()// 开始录音
    },
    handleclicks () {
      recorder.stop() // 结束录音
      // recorder.downloadPCM('1')
    },
    handledownloadWAV () {
      let bold = recorder.getPCMBlob();
      let filesy = new File([bold], 'test.pcm', {type: 'audio/pcm', lastModified: Date.now()});
      var reader = new FileReader();
      reader.readAsDataURL(filesy);
       axios({
            method: 'post',
            data: filesy,
            url:'http://p2fg2f.natappfree.cc/save',
          }).then(res => {
            console.log(res)
          })
    }
  }
}
</script>


